.searchForm {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
}
.inputFilter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 10px;
}
.loading {
  -webkit-animation: rotateEdit 0.5s linear infinite forwards;
          animation: rotateEdit 0.5s linear infinite forwards;
}

@-webkit-keyframes rotateEdit {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes rotateEdit {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }

  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

#searchCog {
  color: orange;
  position: relative;
  top: 10px;
  font-size: 50px;
  display: none;
}

.initialForm {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
}

/* initial size of search text input */
.initialSize {
  text-align: center;
  font-size: 25px;
  height: 55px;
  border: 0;
  padding: 0 5px;
}
#filterSearchBtn {
  background: #3498db;
  font-size: 25px;
  border: 0;
  margin-left: 3px;
}

/* size of input after a search has been sent */
.searchedSize {
  font-size: 20px;
  text-align: center;
  margin: auto;
  height: 35px;
  border: 0;
  padding: 0 5px;
}

.initialSizeBtn {
  color: white;
  background: #3498db;
  text-align: center;
  padding: 15px 30px;
  border: 0;
  margin-left: 1px;
  font-size: 20px;
  -webkit-box-shadow: 0 0.5rem 2rem black;
          box-shadow: 0 0.5rem 2rem black;
  -webkit-transition: -webkit-box-shadow 0.1s;
  transition: -webkit-box-shadow 0.1s;
  -o-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
  transition: box-shadow 0.1s, -webkit-box-shadow 0.1s;
}

.initialSizeBtn:active {
  -webkit-box-shadow: 0 0.125rem 1rem black;
          box-shadow: 0 0.125rem 1rem black;
}

.initialSizeBtn:active,
#filterSearchBtn:active,
.searchedSizeBtn:active {
  background: orange;
}

#initialCog {
  display: block;
  margin: 30px auto;
  color: orange;
  position: relative;
  top: 10px;
  font-size: 125px;
}
.searchedSizeBtn {
  background: #3498db;
  font-size: 20px;
  height: 35px;
  border: 0;
  font-weight: bold;
  -webkit-box-shadow: 0 0.25rem 1rem black;
          box-shadow: 0 0.25rem 1rem black;
  -webkit-transition: -webkit-box-shadow 0.1s;
  transition: -webkit-box-shadow 0.1s;
  -o-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
  transition: box-shadow 0.1s, -webkit-box-shadow 0.1s;
  margin-bottom: 10px;
}

.searchedSizeBtn:active {
  -webkit-box-shadow: none;
          box-shadow: none;
}
#searchCog {
  margin: 2px;
}

@media screen and (min-width: 1000px) {
  .initialSize {
    width: 40%;
    font-size: 30px;
  }
  .initialSizeBtn {
    width: 10%;
    display: block;
    margin: 10px auto;
  }
  .searchForm {
    width: 70%;
  }

  .inputFilter {
    margin: 0;
    display: -ms-flexbox;
    display: flex;
  }

  .searchedSizeBtn {
    display: -ms-flexbox;
    display: flex;
    background: #3498db;
    font-size: 20px;
    margin: 0 3px;
    height: 5vh;
    border: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    font-weight: bold;
    padding: 5px 15px;
  }

  .searchedSize {
    margin: 0;
    font-size: 20px;
    height: 5vh;
    text-align: center;
    padding: 0 5px;
  }

  .searchForm {
    display: -ms-flexbox;
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }
  #filterSearchBtn {
    font-size: 30px;
    color: white;
    background: #3498db;
    padding: 0 15px;
  }
}



